.accordion {
  display: grid;
  gap: 5px 0;

  & summary {
    position: relative;
    padding: 20px 15px;
    margin: 0 -15px;
    font-size: 15px;
    line-height: 20px;
    background-color: var(--gray-background);
    cursor: pointer;

    &:hover {
      background-color: var(--light-gray);
    }
  }

  & summary::marker {
    content: none;
  }

  & summary::-webkit-details-marker {
    display: none;
  }

  & summary::after {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 15px;
    content: "";
    width: 20px;
    height: 20px;
    background-image: url(data:image/svg+xml;base64,);
    background-repeat: no-repeat;
  }

  & [open] summary::after {
    background-image: url(data:image/svg+xml;base64,);
  }

  & h2,
  h3 {
    width: calc(100% - 20px);
    font-size: 15px;
    font-weight: 400;
  }

  & ul {
    display: grid;
    row-gap: 5px;
    margin: 0 -15px;
    padding: 5px 15px 15px;
    font-size: 14px;
    background-color: var(--gray-background);

    & a {
      display: inline-block;
      padding: 5px 0;
      line-height: 18px;
      color: var(--basic-green);
    }
  }

  &--stats h2 {
    font-size: 15px;
    font-weight: 500;
  }
}

.accordion__body {
  display: grid;
  row-gap: 12px;
  margin: 0 -15px;
  padding: 5px 15px 15px;
  font-size: 14px;
  background-color: var(--gray-background);

  & p {
    line-height: 20px;
  }

  & ol {
    display: grid;
    row-gap: 7px;

    & li {
      line-height: 20px;
    }
  }
}

.accordion__table {
  border-spacing: 0 8px;

  & td {
    padding: 0;
    vertical-align: initial;

    &:first-child {
      display: block;
      margin-right: 10px;
    }
  }
}

@media (min-width: @tablet-width) {
  .accordion {
    & summary {
      margin: 0;
      border-radius: 5px;
    }

    & ul,
    .accordion__body {
      margin: 0;
      padding-left: 23px;
      border-radius: 0 0 5px 5px;
    }

    &--stats h2 {
      font-size: 16px;
    }

    &--agents h2 {
      font-size: 16px;
    }
  }
}
