.modal-abuse {
  & .button-main {
    width: 100%;
  }
}

.modal-abuse__select {
  position: relative;
  margin-bottom: 15px;

  &::after {
    content: "";
    position: absolute;
    top: calc(50% - 7px);
    right: 7px;
    width: 14px;
    height: 14px;
    background-image: url(data:image/svg+xml;base64,);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100%;
  }

  & select {
    padding: 14px;
    width: 100%;
    font-size: 16px;
    color: #666666;
    background-color: var(--white);
    white-space: wrap;
    border: 1px solid var(--gray);
    border-radius: 5px;
    appearance: none;

    &:focus-visible {
      box-shadow: 0 0 0 1px #ffffff, 0 0 1px 2px #74d099, 0 0 1px 2px #20a052;
    }
  }
}

.modal-abuse__form {
  & .input,
  & .textarea {
    margin-bottom: 15px;
  }

  & .input {
    padding: 14px 30px 14px 15px;
  }
}
