.doc__header {
  & h1 {
    margin-bottom: 15px;
  }
}

.doc__aside {
  background-color: var(--gray-background);
  border-radius: 7px;
}

.doc__header-action {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  font-size: 14px;

  & a {
    padding: 5px 35px 5px 5px;
    border-radius: 5px;
    background-image: url(data:image/svg+xml;base64,);
    background-repeat: no-repeat;
    background-position: 95%, center;

    &:hover {
      background-color: var(--light-gray);
    }
  }
}

.doc__header-info {
  display: flex;
  align-items: center;
  column-gap: 20px;
  margin-bottom: 20px;
  font-size: 14px;
  color: #737a8e;

  & p:last-of-type {
    margin-left: auto;
  }
}

.doc__header-img {
  margin-bottom: 20px;
  height: 170px;
  border-radius: 7px;
}

.doc__header-img,
.doc__body-picture {
  & img {
    height: 100%;
    object-fit: cover;
  }
}

.doc__body-picture {
  height: 270px;
}

.doc__description {
  margin-bottom: 30px;
  font-size: 19px;
  line-height: 28px;
  font-weight: 500;
}

.doc__body {
  & h2 {
    margin-top: 30px;
  }

  & p {
    font-size: 17px;
    line-height: 25px;
  }

  & > p {
    margin-top: 12px;
  }

  & h3 {
    margin-top: 25px;
  }
}

.doc__body-picture,
.doc__body-figure {
  margin-top: 20px;
  margin-bottom: 20px;
}

.doc__body-offtop {
  margin-top: 25px;
  margin-bottom: 25px;
}

.doc__body-list {
  margin-top: 20px;
  margin-bottom: 20px;
}

.doc__related-articles {
  margin-top: 30px;
}

@media (min-width: @tablet-width) {
  .doc__header {
    & h1 {
      margin-bottom: 20px;
    }
  }

  .doc__header-img {
    height: 300px;
  }

  .doc__description {
    font-size: 19px;
    line-height: 28px;
  }

  .doc__nav {
    padding: 20px 25px 20px;
    box-shadow: 0 0 10px var(--light-gray);
    border-radius: 7px;
  }

  .doc__body {
    & h2 {
      margin-top: 40px;
    }

    & p {
      font-size: 18px;
      line-height: 27px;
    }
  }

  .doc__body-picture {
    height: 350px;
  }
}

@media (min-width: @desktop-width) {
  .doc {
    display: grid;
    grid-template-columns: 300px 1fr;
    gap: 20px;
  }

  .doc__header-info {
    & p:last-of-type {
      margin-left: 50px;
    }
  }

  .doc__header-img {
    height: 340px;
  }

  .doc__description {
    margin-bottom: 25px;
    font-size: 19px;
    font-size: 21px;
    line-height: 30px;
  }

  .doc__body-picture {
    height: 400px;
  }
}
